import React from "react"
import { Link } from "@reach/router";
import { Wrapper, Logo, Content, Heading, SubHeading } from "./styles"
import logo from "assets/images/cambio-online-white.png"

function Hero() {
  return (
    <Wrapper>
      <Logo as={Link} to="/">
        <img src={logo} alt="Cambio Online" />
      </Logo>
      <Content>
        <Heading >cadastra-se</Heading>
        <SubHeading>
          Todos os Dias, Cambio Online  faz milhares de clientes felizes..
        </SubHeading>
      </Content>
    </Wrapper>
  )
}

export default Hero
