import React, { Fragment, useState, useContext } from "react";
import { Link } from "@reach/router";
import { Contents } from "components/common/";
import { Wrapper, Logo, Nav } from "./styles";
import logo from "assets/images/cambio-online.png";

import Hamburger from "./Hamburger";
import NavbarLinks from "./NavbarLinks";
import LoginBottons from "./LoginBottons";
import Drawer from "./Drawer";

function NavBar() {
  const [toggle, setToggle] = useState(false);
  return (
    <Fragment>
      <Wrapper as={Contents}>
        <Logo>
          <Link to="/">
            <img
              src={logo}
              style={{ width: 120, height: 80 }}
              alt="cambio online"
            />
          </Link>
        </Logo>
        <Hamburger setToggle={() => setToggle(!toggle)} toggle={toggle} />
        <Nav>
          <NavbarLinks />
        </Nav>
        <Nav>
          <LoginBottons />
        </Nav>
      </Wrapper>
      <Drawer toggle={toggle} />
    </Fragment>
  );
}

export default NavBar;
