import styled from "styled-components"

export const Heading = styled.h2`
  font-size: 2.25rem;
  text-align: center;
`

export const SubHeading = styled.p`
  font-size: 1.125rem;
  text-align: center;
  margin-bottom: 1.5rem;
`

export const AppStore = styled.img`
  margin-left: 1rem;
  margin-right: 1rem;
  margin-bottom: 5px;
  cursor: pointer;
`

export const GoogleStore = styled.img`
  margin-left: 1rem;
  margin-right: 1rem;
  margin-bottom: 5px;
  cursor: pointer;
`

export const Float = styled.div`
  position:fixed;
  width:45px;
  height:45px;
  bottom:40px;
  background-color:#25d366;
  color:#FFF;
  border-radius:50px;
  text-align:center;
  font-size:30px;
  box-shadow: 2px 2px 3px #999;
  z-index:100;
`; 

export const myFloat = styled.div`
  margin-top: 16px;
`
