import React from "react";
import { LinkNav } from "./styles";



function LoggedIn() {
  return (
    <>
      <LinkNav to="/send-request" aria-label="Your account settings">
        ENVIAR
      </LinkNav>

      <LinkNav to="/receber" aria-label="Receber">
        RECEBER
      </LinkNav>
    </>
  );
}

export default LoggedIn;
