import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  Wrapper,
  Banner,
  BannerContents,
  BannerTransactionName,
  BannerTransactionAmount,
  BannerTransactionDate,
  Details,
  Divider,
  Title,
  TransactionContents,
  List,
  CloseButton,
  AccessibleText,
  Item,
  TransactionAmount,
  TransactionTotal,
  Total
} from "./styles";

export default function RecebeDash({ closeModal, data }) {
  return (
    <Wrapper onClick={e => e.stopPropagation()}>
      <Banner>
        <BannerContents>
          <FontAwesomeIcon icon="building" size="5x" color="#ffffff" />
          <BannerTransactionName>Cambio Online</BannerTransactionName>
          <BannerTransactionAmount></BannerTransactionAmount>
          <smaill style={{color: "#fff"}}>Enviaremos os Dados de nosso banco intermediario via E-mail para ser realizado o Pagamento suporte@cambioonline.shop</smaill>
          <BannerTransactionAmount></BannerTransactionAmount>
          <BannerTransactionDate></BannerTransactionDate>
        </BannerContents>
      </Banner>
      <Details>
        
        <Title>Dados do Recebimento

        <CloseButton aria-label="close modal" onClick={closeModal}>
            <FontAwesomeIcon icon="times" size="xs" />
            <AccessibleText>Close</AccessibleText>
          </CloseButton>
        </Title>
        <Divider mt="1rem" mb="1rem" />
        <TransactionContents>
          <List>
            <Item medium>
              Email:
              <TransactionAmount>
                {data.email}
              </TransactionAmount>
            </Item>
          </List>
          <Divider mt="1rem" mb="0.5rem" />
          <TransactionTotal>
            <Total>
              RECEBER:{data.send} 
            </Total>
            
          </TransactionTotal>
          
          <List>
            <Item medium>
            PAGAR: {data.recipient}
            </Item>
            <Item color="#8e9a9d"></Item>
          </List>
          <List>
            <Item medium>MOEDAS A RECEBER</Item>
            <Item color="#8e9a9d">{data.sendCurrency}</Item>
          </List>

          <List>
            <Item medium>MOEDAS A PAGAR</Item>
            <Item color="#8e9a9d">{data.recipientCurrency}</Item>
          </List>
        </TransactionContents>
      </Details>
    </Wrapper>
  );
}
