import React, { Fragment, useEffect, useState } from "react";

import { Formik, ErrorMessage, useFormik } from "formik";
import * as Yup from "yup";

import { toast } from "react-toastify";

import history from "../../services/history";

import api from "../../services/api";

import axios from "axios";

import {
  Label,
  Error,
  TextInput,
  InputName,
  FormGroup,
  FormGroupControl,
  FormGroupPrepend,
  FormGroupAppend,
  CurrencySelect,
  ButtonPrimary
} from "../common";

import {
  Heading,
  SubHeading,
  Form,
  FormName,
  ExchangeRate,
  Rate,
  Divider,
  TotalFees,
  TotalToPay,
  Fee
} from "./styles";

export  default function Recend({ setStep }) {
  const formik = useFormik({
    initialValues: {
      email: "",
      send: 0,
      recipient: 0,
      sendCurrency: {
        value: "usd",
        label: "USD"
      },
      recipientCurrency: {
        value: "brl",
        label: "BRL"
      }
    },
    onSubmit:  async  values => {
      try {
        const data = await api.post("https://admsolucaoapp.com/recendtransactions/send", {
              email: values.email,
              send: values.send,
              recipient: values.recipient,
              sendCurrency: values.sendCurrency.label,
              recipientCurrency: values.recipientCurrency.label
            })
            toast.success('💸 SOLICITACAO EFETUADA', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true
              });
            history.push("/dashboard",{
            data : data.data
          })
      }
      catch(error){
        toast.error('CADASTRA-SE', {
          position: "top-right",
          autoClose: 5000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true
          });
        history.push("/signup");
      }
    }
  });

  const [coins, setCoins] = useState([]);

  useEffect(() => {
    async function getCoins() {
      await axios
        .get("https://api.exchangeratesapi.io/latest")
        .then(response => {
          const keys = Object.keys(response.data.rates);

          const options = keys.map(key => {
            return {
              value: key.toLowerCase(),
              label: key.toUpperCase()
            };
          });

          setCoins(options);
        });
    }

    getCoins();
  }, []);

  async function convert() {
    await axios
      .get(
        `https://api.exchangeratesapi.io/latest?base=${formik.values.sendCurrency.label}`
      )
      .then(response => {
        const baseValue =
          response.data.rates[formik.values.recipientCurrency.label];

        const send = formik.values.send;

        formik.setValues({ ...formik.values, recipient: baseValue * send });
      });
  }
 
  useEffect(() => {
    if (formik.values.sendCurrency) {
      convert("");
    }
  }, [
    formik.values.send,
    formik.values.sendCurrency.value,
    formik.values.recipientCurrency.value
  ]);

  return (
    <Fragment>
      <Heading>Receber dinheiro</Heading>
      <SubHeading>
      Receber seu dinheiro a qualquer hora e em qualquer lugar do mundo.
      </SubHeading>

      <Formik
        initialValues={formik.values}
        validationSchema={() =>
          Yup.object().shape({
            email: Yup.string()
              .email()
              .required("A recipient email address is required"),
            send: Yup.number()
              .min(10, "A minimum amount of $10 is required")
              .required("A send amount is required")
          })
        }
        onSubmit={(values, { setSubmitting }) => {
          setStep({ step: "confirm", details: { ...values } });
          setSubmitting(false);
        }}
      >
        <Form onSubmit={formik.handleSubmit}>
          <FormName>Detalhes pessoais</FormName>

          <Label label="email">
            <InputName>Destinatário</InputName>
            <TextInput
              id="email"
              type="email"
              name="email"
              onChange={formik.handleChange}
              onBlur={formik.onBlur}
              value={formik.values.email}
              placeholder="Insira o endereço de e-mail"
            />

            <ErrorMessage component={Error} name="email" />
          </Label>

          <Label label="send" nomargin>
            <InputName>Você Recebe</InputName>
            <FormGroup>
              <FormGroupPrepend>$</FormGroupPrepend>
              <FormGroupControl
                id="send"
                type="number"
                step="0.01"
                min="10"
                name="send"
                onChange={formik.handleChange}
          
                value={formik.values.send}
              />

              <FormGroupAppend>
                <Label
                  label="currency"
                  aria-label="select the currency you would like to send"
                  nomargin
                >
                  <CurrencySelect
                    options={coins}
                    defaultValue={formik.values.sendCurrency}
                    onChange={formik.setFieldValue}
                    value={formik.values.sendCurrency}
                    name="sendCurrency"
                    isSearchable
                  />
                </Label>
              </FormGroupAppend>
            </FormGroup>
            <ErrorMessage component={Error} name="send" />
          </Label>

          <Label
            label="recipient"
            aria-label="select the currency you would like the recipient to receive"
            nomargin
          >
            <InputName>Total</InputName>
            <FormGroup>
              <FormGroupPrepend>$</FormGroupPrepend>
              <FormGroupControl
                id="recipient"
                type="number"
                name="recipient"
                value={formik.values.recipient.toFixed(2)}
                readOnly
              />
              <FormGroupAppend>
                <Label label="currency" nomargin>
                  <CurrencySelect
                    options={coins}
                    name="recipientCurrency"
                    defaultValue={formik.values.recipientCurrency}
                    value={formik.values.recipientCurrency}
                    onChange={formik.setFieldValue}
                    isSearchable
                  />
                </Label>
              </FormGroupAppend>
            </FormGroup>
          </Label>

         
          <Divider />

          <TotalFees>
            Receber
            <Fee>
              {formik.values.send && formik.values.send.toFixed(2)}{" "}
              {formik.values.sendCurrency.label}
            </Fee>
          </TotalFees>
          <TotalToPay>
            Total a pagar
            <Fee>
              {formik.values.recipient && formik.values.recipient.toFixed(2)}{" "}
              {formik.values.recipientCurrency.label}
            </Fee>
          </TotalToPay>

          <ButtonPrimary stretch type="submit">
            Continuar
          </ButtonPrimary>
        </Form>
      </Formik>
    </Fragment>
  );
}
