import React from "react";
import { LinkNav, SignupButton } from "./styles";

function userNotLoggedIn({ onClick }) {
  return (
    <>
      <LinkNav to="/login" aria-label="Your account settings">
        Login
      </LinkNav>
      <SignupButton onClick={onClick} aria-label="Sign up for payyed">
        Registrar
      </SignupButton>
    </>
  );
}

export default userNotLoggedIn;
