import React from "react"
import cards from "./cards"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { Wrapper, Contents, Row, ButtonLink } from "../../common"
import { Info, Cards, Card, Heading, SubHeading } from "./styles"

function PaymentSolutions() {
  return (
    <Wrapper bgColor="#f5f5f5">
      <Contents>
        <Row>
          <Info>
            <Heading>Soluções de pagamento para qualquer pessoa.</Heading>
            <SubHeading>
            Transferências internacionais de dinheiro.
            Enviar dinheiro para sua conta no exterior
            Investir em outro país
            Mandar dinheiro para alguém da família
            Transferência de dinheiro para um terceiro
            Enviar dinheiro para um estudante no exterior
            Pagar MBA, intercâmbio, high-school ou outros cursos
            Pagar aluguel de imóvel no exterior
            Pagar a compra de um imóvel no exterior
            Receber uma transferência de sua conta exterior
            Receber salário do exterior
            Receber pagamento por um serviço
            Envio de dinheiro para o exterior de um jeito fácil.
            </SubHeading>
            <ButtonLink fontSize="1.125rem" href="#">
             Encontre mais soluçõess{" "}
              <FontAwesomeIcon
                icon="chevron-right"
                transform="down-1 shrink-3 right-4"
              />
            </ButtonLink>
          </Info>
          <Cards>
            {cards.map((card, index) => (
              <Card
                key={index}
                content={card.alt}
                image={card.image}
                aria-label={card.alt}
              />
            ))}
          </Cards>
        </Row>
      </Contents>
    </Wrapper>
  )
}

export default PaymentSolutions
