import React from "react";
import { useDispatch, useSelector } from "react-redux";

import { signInRequest } from "../../../store/modules/auth/actions";

import { Formik, ErrorMessage } from "formik";
import * as Yup from "yup";
import {
  Form,
  FormName,
  Error,
  InputGroup,
  Label,
  TextInput,
  InputName,
  CheckboxInputLabel,
  CheckboxInput,
  CheckboxInputName,
  StyledCheckboxInput,
  Account,
  ButtonLink,
  ButtonPrimary
} from "../../common";
import { Wrapper } from "./styles";

function LoginForm() {
  const dispatch = useDispatch();
  const loading = useSelector(state => state.auth.loading);

  const handleSubmit = async (
    { email, password },
    { setSubmitting, setFieldValue }
  ) => {
    dispatch(signInRequest(email, password));

    setSubmitting(false);
    setFieldValue("submitError", true);
  };

  return (
    <Wrapper>
      <Formik
        initialValues={{
          email: "",
          password: "",
          rememberMe: false,
          submitError: false
        }}
        validationSchema={() =>
          Yup.object().shape({
            email: Yup.string()
              .email()
              .required("Use seu E-mail"),
            password: Yup.string().required("Use sua Senha")
          })
        }
        onSubmit={handleSubmit}
      >
        {({
          setFieldValue,
          handleBlur,
          handleChange,
          handleSubmit,
          values
        }) => (
          <Form onSubmit={handleSubmit}>
            <FormName>LOGIN</FormName>

            <Label label="email">
              <InputName>E-mail</InputName>
              <TextInput
                onChange={handleChange}
                onBlur={handleBlur}
                type="email"
                name="email"
                placeholder="Use seu E-mail"
              />
              <ErrorMessage component={Error} name="email" />
            </Label>

            <Label label="password">
              <InputName>Senha</InputName>
              <TextInput
                onChange={handleChange}
                onBlur={handleBlur}
                type="password"
                name="password"
                placeholder="Use sua senha"
              />
              <ErrorMessage component={Error} name="password" />
            </Label>

            <InputGroup>
              <CheckboxInputLabel label="Lembrar-me">
                <CheckboxInput
                  onChange={() =>
                    setFieldValue("rememberMe", !values.rememberMe)
                  }
                  checked={values.rememberMe}
                  type="checkbox"
                  name="remember-me"
                />
                <StyledCheckboxInput checked={values.rememberMe} />
                <CheckboxInputName>Lembrar Me</CheckboxInputName>
              </CheckboxInputLabel>

              <ButtonLink href="#" fontSize="14px">
                esqueceu sua senha?
              </ButtonLink>
            </InputGroup>

            <ButtonPrimary stretch type="submit">
              {loading ? "Carregando..." : "LOGIN"}
            </ButtonPrimary>
          </Form>
        )}
      </Formik>

      <Account>
        Não possui uma conta?{" "}
        <ButtonLink  href="/signup">
         Inscrever-se
        </ButtonLink>
      </Account>
    </Wrapper>
  );
}

export default LoginForm;
