import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  Wrapper,
  Banner,
  BannerContents,
  BannerTransactionName,
  BannerTransactionAmount,
  BannerTransactionDate,
  Details,
  Divider,
  Title,
  TransactionContents,
  List,
  CloseButton,
  AccessibleText,
  Item,
  TransactionAmount,
  TransactionTotal,
  Total
} from "./styles";

export default function ModalDash({ closeModal, data }) {
  return (
    <Wrapper onClick={e => e.stopPropagation()}>
      <Banner>
        <BannerContents>
          <FontAwesomeIcon icon="building" size="5x" color="#ffffff" />
          <BannerTransactionName>Cambio Online</BannerTransactionName>
          <small style={{color: '#FFF'}}>Dados para  Pagamento </small>
          <BannerTransactionName>Santander Brasil Copyright (c) </BannerTransactionName>
          <BannerTransactionName>agencia: <small>0831</small> </BannerTransactionName>
          <BannerTransactionName>conta: <small>00013-001019-9</small> </BannerTransactionName>
          <BannerTransactionName>cnpj: <small>30.904.491/0001-66</small> </BannerTransactionName>
          <BannerTransactionAmount></BannerTransactionAmount>
          <BannerTransactionDate></BannerTransactionDate>
        </BannerContents>
      </Banner>
      <Details>
        
        <Title>Detalhes da transação

        <CloseButton aria-label="close modal" onClick={closeModal}>
            <FontAwesomeIcon icon="times" size="xs" />
            <AccessibleText>Close</AccessibleText>
          </CloseButton>
        </Title>
        <Divider mt="1rem" mb="1rem" />
        <TransactionContents>
          <List>
            <Item mb="0.5rem">
            Valor do pagamento
              <TransactionAmount>
                ${data.send} {data.sendCurrency}{" "}
              </TransactionAmount>
            </Item>
            <Item mb="0.5rem">
              <TransactionAmount></TransactionAmount>
            </Item>
          </List>
          <Divider mt="1rem" mb="0.5rem" />
          <TransactionTotal>
           Valor Total{" "}
            <Total>
              {data.recipient} - {data.recipientCurrency}
            </Total>
          </TransactionTotal>
          <List>
            <Item medium>
            Pago pelo: $ {data.send} {data.sendCurrency}{" "}
            </Item>
            <Item color="#8e9a9d"></Item>
          </List>
          <List>
            <Item medium>Transação ID:</Item>
            <Item color="#8e9a9d">26566689645685976589</Item>
          </List>

          <List>
            <Item medium>Status: {data.status ? "aprovado" : "pendente"}</Item>
            <Item color="#8e9a9d"></Item>
          </List>
        </TransactionContents>
      </Details>
    </Wrapper>
  );
}
