import React from "react"

import { Switch } from 'react-router-dom';
import Route from './Route';

//components VIEWS  BASE 

import Landing from '../components/Landing';
import DashBoard from "../components/Dashboard";
import SendRequest from "../components/SendRequest";
import Recend from "../components/Recend";
import SendSignup from "../components/SendRequest";

import LoginForm from "../components/Login";
import SignUp from "../components/Signup";
import BendRequest from "components/BenRequest";

//component VIEWS IsPrivate


export default function Routes() {
  return (
    <Switch>
      <Route path="/" exact component={Landing} />
      <Route path="/dashboard"  component={DashBoard}  isPrivate />
      <Route path="/send-request" component={SendSignup} />
      <Route path="/signup"   component={SignUp} />
      <Route path="/send-request-money" exact component={SendRequest}  isPrivate/>
      <Route path="/send-recend" exact component={Recend}  isPrivate/>
      <Route path="/receber" exact component={Recend}  />
      <Route path="/bentransactions"exact component={BendRequest} isPrivate />
      
      <Route path="/login"   component={LoginForm} />

    </Switch>
  );
}
