import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import TransactionStatus from "./Status";
import RecebeDash from "../../RecebeDash";
import { Modal } from "../Modal";

import { Row, Col } from "..";
import {
  Wrapper,
  TransactionName,
  TransactionDescription,
  TransactionAmount,
  Date,
  Day,
  Month,
  Amount,
  Currency,
  Sign
} from "./styles";

import api from "../../../services/api";

export function Receber() {
  const [showModal, setShowModal] = useState(false);
  const closeModal = () => setShowModal(false);
  const openModal = () => setShowModal(true);

  const [RecendActive, setRecend] = useState([]);
  const [RecendPosition, setRecendPosition] = useState("");



  useEffect(() => {
    (async () => {
      const result = await api.get("/recend/send/clients");
      const data = result.data;

      if (data.length) {
        setRecend(data);
       
      }
    })();
  }, []);

  const handleWrapper = position => {
    setRecendPosition(position);
    openModal();
  };

  return (
    <>
      {RecendActive.map((activity, x) => (
        <Wrapper
        onClick={() => {
          handleWrapper(x);
        }}
        key={x}
        >

         
          <Row>
           
            <Col flexSM="4" flexMD="2" flex="5"  
        
            >
              
              <TransactionDescription>
                {activity.email}
              </TransactionDescription>
            </Col>
           
            <Col >
              <TransactionAmount>
           
                <Amount>R${activity.send}</Amount>
              
               
              </TransactionAmount>
            </Col>
          </Row>
    
         
         
        </Wrapper>
      ))}
        
      {showModal && (
        <Modal closeModal={closeModal} noCloseButton>
          {
            <RecebeDash
              data={RecendActive[RecendPosition]}
            
              closeModal={closeModal}
            />
          }
        </Modal>
      )}
    </>
  );
}
