import React from "react"
import Slick from "react-slick"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {
  Wrapper,
  Contents,
  Row,
  ButtonLink
} from "../../common"
import {
  Heading,
  SubHeading,
  Slider,
  Testimonial,
  Review,
  User,
  Location
} from "./styles"
import testimonials from "./testimonials"

function WhatPeopleAreSayingAboutPayyed() {
  const settings = {
    accessibility: true,
    adaptiveHeight: true,
    dots: true,
    arrows: true,
    autoplay: true,
    slidesToShow: 2,
    slidesToScroll: 2,
    autoplaySpeed: 8000,
    variableWidth: true,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          infinite: true,
          dots: true,
          arrows: false
        }
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: true,
          dots: true,
          arrows: false
        }
      }
    ]
  }

  return (
    <Wrapper>
      <Contents>
        <Heading>O que as pessoas estão dizendo sobre Cambio Online</Heading>
        <SubHeading>Uma experiência de pagamento que as pessoas gostam de falar</SubHeading>
        <Slider as={Slick} {...settings}>
          {testimonials.map((testimonial, index) => (
            <Testimonial key={index}>
              <Review>{testimonial.review}</Review>
              <User>{testimonial.user}</User>
              <Location>{testimonial.location}</Location>
            </Testimonial>
          ))}
        </Slider>
        <Row justify="center">
          <ButtonLink href="#" fontSize="1.125rem">
           Ver mais pessoas comentando{" "}
            <FontAwesomeIcon icon="chevron-right" transform="down-1 shrink-3 right-4" />
          </ButtonLink>
        </Row>
      </Contents>
    </Wrapper>
  )
}

export default WhatPeopleAreSayingAboutPayyed
