import React from "react"
import cards from "./cards"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { Wrapper, Contents, Row, ButtonLink } from "../../common"
import { Card, Icon, Heading, SubHeading } from "./styles"

function WhatCanYouDoWithPayyed() {
  return (
    <Wrapper>
      <Contents>
        <Heading>O que você pode fazer com Cambio Online?</Heading>
        <Row justify="space-between">
          {cards.map((card, index) => (
            <Card key={index} content={card.alt} aria-label={card.alt}>
              <Icon>
                <a  href="/signup">
                <FontAwesomeIcon
                  icon={card.icon}
                  size="4x"
                  transform="shrink-2"
                  color="#2dbe60"
                />
               </a>
              </Icon>
            </Card>
          ))}
        </Row>
        <Row justify="center">
          <ButtonLink href="#" fontSize="1.125rem">
            Veja mais que você pode fazer{" "}
            <FontAwesomeIcon
              icon="chevron-right"
              transform="down-1 shrink-3 right-4"
            />
          </ButtonLink>
        </Row>
      </Contents>
    </Wrapper>
  )
}

export default WhatCanYouDoWithPayyed
