import React from "react";
import { LinkNav, SignoutButton } from "./styles";

function userLoggedIn({ onClick }) {
  return (
    <>
      <LinkNav to="/dashboard" aria-label="Your account settings">
        DashBoard
      </LinkNav>
      <SignoutButton aria-label="Sign out of your account" onClick={onClick}>
        Sair
      </SignoutButton>
    </>
  );
}

export default userLoggedIn;
