import React from "react";
import { LinkNav } from "./styles";

function sendNotLoggedIn({ onClick }) {
  return (
    <>
      <LinkNav to="/send-request-money" aria-label="Your account settings">
        ENVIAR
      </LinkNav>

      <LinkNav to="/send-recend" aria-label="Your account settings">
        RECEBER
      </LinkNav> 
    </>
  );
}

export default sendNotLoggedIn;
