import React, { useEffect } from "react";
import { useDispatch } from "react-redux";
import { Link } from "@reach/router";
import { Formik, ErrorMessage } from "formik";
import * as Yup from "yup";
import { Wrapper } from "./styles";
import {
  Form,
  FormName,
  Error,
  Label,
  TextInput,
  InputName,
  Account,
  ButtonLink,
  ButtonPrimary
} from "../../common";

import { signUpRequest } from "../../../store/modules/auth/actions";

function Signup() {
  const dispatch = useDispatch();

  function handleSubmit({ name, email, phone, password, conf_password }) {
    dispatch(signUpRequest(name, email, phone, password, conf_password));
  }


  function gtag_report_conversion(url) {
    var callback = function () {
      if (typeof(url) != 'undefined') {
        window.location = url;
      }
    };
    window.gtag('event', 'conversion', {
        'send_to': 'AW-740590632/CaqKCMflgMcBEKiIkuEC',
        'event_callback': callback
    });
    return false;
  }

  

  return (

    
    <Wrapper>
      <Formik
        initialValues={{
          name: "",
          email: "",
          phone: "",
          password: "",
          conf_password: "",
          submitError: false
        }}
        validationSchema={() =>
          Yup.object().shape({
            name: Yup.string()
              .trim()
              .matches(
                /[\w\s]$/,
                "Name can only contain letters, and cannot include any special characters, punctionations or start with a space"
              )
              .required("Preencha seu nome completo"),
            phone: Yup.string()
              .matches(
                /[\w\s]$/,
                "Name can only contain letters, and cannot include any special characters, punctionations or start with a space"
              )
              .required("Seu telefone"),
            email: Yup.string()
              .email()
              .required("Seu E-mail"),
            password: Yup.string().required("Sua Senha !"),

            conf_password: Yup.string().required("Confirme Sua Senha !")
          })
        }
        onSubmit={handleSubmit}
      >
        {({ values, handleChange, handleBlur, handleSubmit }) => (
          <Form onSubmit={handleSubmit}>
            <FormName>Cadastra-se</FormName>

            <Label label="name">
              <InputName>Nome completo</InputName>
              <TextInput
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.name}
                type="text"
                name="name"
                placeholder="Nome completo"
              />
              <ErrorMessage component={Error} name="name" />
            </Label>

            <Label label="email">
              <InputName>E-mail</InputName>
              <TextInput
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.email}
                type="email"
                name="email"
                placeholder="E-mail"
              />
              <ErrorMessage component={Error} name="email" />
            </Label>

            <Label label="phone">
              <InputName>Telephone</InputName>
              <TextInput
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.phone}
                type="cell"
                name="phone"
                placeholder="Telephone"
              />
              <ErrorMessage component={Error} name="phone" />
            </Label>

            <Label label="password">
              <InputName>Senha</InputName>
              <TextInput
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.password}
                type="password"
                name="password"
                placeholder="Senha"
              />
              <ErrorMessage component={Error} name="password" />
            </Label>

            <Label label="conf_password">
              <InputName>Confirmar senha</InputName>
              <TextInput
                onChange={handleChange}
                onBlur={handleBlur}
                value={values.conf_password}
                type="password"
                name="conf_password"
                placeholder="Confirme sua senha"
              />
              <ErrorMessage component={Error} name="conf_password" />
            </Label>

            <ButtonPrimary stretch margin="0.5rem 0" gtag_report_conversion type="submit">
              CADASTRAR
            </ButtonPrimary>
          </Form>
        )}
      </Formik>

      <Account>
        Ja possui uma conta?{" "}
        <ButtonLink style={{ color: "#013689" }}   href="/login">
          Login
        </ButtonLink>
      </Account>
    </Wrapper>
  );
}

export default Signup;
