import React from "react"
import { Wrapper, Contents, Row, ButtonSecondary } from "../../common"
import { FixedBackground, Heading, SubHeading } from "./styles"

function AwesomeCustomerSupport() {
  return (
    <Wrapper as={FixedBackground}>
      <Contents>
        <Heading>Suporte ao cliente impressionante</Heading>
        <SubHeading>
        Você tem alguma dúvida? Não se preocupe. Temos ótimas pessoas prontas para ajudar
        você sempre que precisar.
        </SubHeading>
        <Row justify="center">
          <a href="/signup">
          <ButtonSecondary>Descubra mais</ButtonSecondary>
          </a>
        </Row>
      </Contents>
    </Wrapper>
  )
}

export default AwesomeCustomerSupport
