import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import TransactionStatus from "./Status";
import BenDash from "../../BenDash";
import { Modal } from "../Modal";

import { Row, Col } from "..";
import {
  Wrapper,
  TransactionName,
  TransactionDescription,
  TransactionAmount,
  Date,
  Day,
  Month,
  Amount,
  Currency,
  Sign
} from "./styles";

import api from "../../../services/api";

export function Ben({ location }) {
  const [showModal, setShowModal] = useState(false);
  const closeModal = () => setShowModal(false);
  const openModal = () => setShowModal(true);

  const [benActive, setBenActive] = useState([]);
  const [benPosition, setBenPosition] = useState("");



  useEffect(() => {
    (async () => {
      const result = await api.get("/bendtransactions/send/clients");
      const data = result.data;

      if (data.length) {
        setBenActive(data);
        
      }
    })();
  }, []);

  const handleWrapper = position => {
    setBenPosition(position);
    openModal();
  };

  return (
    <>
      {benActive.map((activity, x) => (
        <Wrapper
        onClick={() => {
          handleWrapper(x);
        }}
        key={x}
        >

         
          <Row>
           
            <Col flexSM="4" flexMD="2" flex="5"  
        
            >
              
              <TransactionDescription>
                {activity.name}
              </TransactionDescription>
            </Col>
           
            <Col >
              <TransactionAmount>
           
                <Amount>({activity.cod})</Amount>
              
               
              </TransactionAmount>
            </Col>
          </Row>
    
         
         
        </Wrapper>
      ))}
        
      {showModal && (
        <Modal closeModal={closeModal} noCloseButton>
          {
            <BenDash
              data={benActive[benPosition]}
            
              closeModal={closeModal}
            />
          }
        </Modal>
      )}
    </>
  );
}
