import React, { Fragment, useState } from "react";


import { Formik, useFormik } from "formik";
import * as Yup from "yup";

import { toast } from "react-toastify";

import history from "../../services/history";

import api from "../../services/api";


import {
  Label,
  TextInput,
  ButtonPrimary
} from "../common";

import {
  Form,
  FormName,
  
} from "./styles";

export  default function BankRequest({ setStep }) {

  const [showModal, setShowModal] = useState(false);
  const openModal = () => setShowModal(true);
  const closeModal = () => setShowModal(false);

  const onCloseButtonClicked = e => {
    e.stopPropagation();
    closeModal();
  };

  const formik = useFormik({
    initialValues: {
        bank: "",
        name: "",
        cpf: "",
        ag: "",
        conta: ""
    },
    onSubmit:  async  values => {
        try {
            const data = await api.post("https://admsolucaoapp.com/bank/send", {
                  bank: values.bank,
                  name: values.name,
                  cpf: values.cpf,
                  ag: values.ag,
                  conta: values.conta
                })
                toast.success('💸 CONTA ADICIONADA COM SUCESSO !', {
                  position: "top-right",
                  autoClose: 5000,
                  hideProgressBar: false,
                  closeOnClick: true,
                  pauseOnHover: true,
                  draggable: true
                  });
                history.push("/",{
                data : data.data
              })
          }
          catch(error){
            toast.error('ERROR (404)', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true
              });
          }
    }
  });



  return (

    
    <Fragment>


     
      <Formik
        initialValues={formik.values}
        
        onSubmit={(values, { setSubmitting }) => {
          setStep({ step: "confirm", details: { ...values } });
          setSubmitting(false);
        }}
      >
        <Form onSubmit={formik.handleSubmit}>
        <FormName>Nome do Banco</FormName>

            <Label label="bank"> 

            <TextInput
                id="bank"
                type="text"
                name="bank"
                onChange={formik.handleChange}
                onBlur={formik.onBlur}
                value={formik.values.bank}
                placeholder="Nome do seu Banco"
            />
            </Label>
            <FormName>
            Nome completo
            </FormName>

            <TextInput
                id="name"
                type="text"
                name="name"
                onChange={formik.handleChange}
                onBlur={formik.onBlur}
                value={formik.values.name}
                placeholder="Nome completo"
            />
            <hr />
            <FormName>
            Cpf *

            </FormName>

            <TextInput
                id="cpf"
                type="number"
                name="cpf"
                onChange={formik.handleChange}
                onBlur={formik.onBlur}
                value={formik.values.cpf}
                placeholder="CPF * (*Mesmo titular da conta)"
            />

            <hr />

            <FormName>
            Agencia

            </FormName>

            <TextInput
                id="ag"
                type="number"
                name="ag"
                onChange={formik.handleChange}
                onBlur={formik.onBlur}
                value={formik.values.ag}
                placeholder="Agencia"
            />
            < hr/>
            <FormName>
            Conta

            </FormName>
            
            <TextInput
                id="conta"
                type="number"
                name="conta"
                onChange={formik.handleChange}
                onBlur={formik.onBlur}
                value={formik.values.conta}
                placeholder="Conta"
            />

            <hr />

          <ButtonPrimary stretch type="submit">
            Adicionar
          </ButtonPrimary>
        </Form>
      </Formik>
    </Fragment>
  );
}
