import React, { useState } from "react";
import { useSelector } from "react-redux";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { toast } from "react-toastify";
import {
  Wrapper,
  ProfileImage,
  Image,
  User,
  EditImage,
  EditImageInput,
  EditProfile
} from "./styles";

import in_array from "../../../functions/in_array";
import api from "../../../services/api";

export function Profile() {
  const [focused, setFocus] = useState(false);
  const setFocusState = () => setFocus(!focused);

  const isLogin = useSelector(state => {
    return state.auth && state.auth.token && state.auth.signed ? true : false;
  });

  const user = useSelector(state => {
    return state.auth.user;
  });

  const [profilePicture, setProfilePicture] = useState(

    `${user.picture.url}?_t=${Date.now()}`
  );

  const handleUploadProfilePic = event => {
    const optionsToast = {
      position: "top-right",
      autoClose: 3000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: false,
      draggable: false
    };

    const fileList = event.target.files;
    const fileExists = typeof fileList[0] !== "undefined";
    let success = false;

    if (fileExists) {
      const data = new FormData();

      const file = fileList[0];
      const types = ["image/jpeg", "image/png"];

      if (in_array(file.type, types)) {
        toast.warn("Enviando!", optionsToast);

        (async () => {
          data.append("file", file);

          success = true;
          const result = await api.put("/files/perfil", data);

          if (result.status === 200) {
            const { url } = result.data;

            toast.success("Enviado com sucesso!", optionsToast);
            setProfilePicture(`${url}?_t=${Date.now()}`);
          } else {
            success = false;
          }
        })();
      }
    }

    if (!success) {
      toast.error("Arquivo invalido, tente novamente!", optionsToast);
    }
  };

  return (
    <Wrapper>

      {profilePicture && 
        <ProfileImage>
        <Image src={profilePicture} alt="foto perfil" key={profilePicture} />
          
          <EditImage aria-label="Enviar photo" focused={focused}>
            <FontAwesomeIcon
              icon="camera"
              size="1x"
              transform="grow-2"
              color="#ffffff"
            />
            <EditImageInput
              type="file"
              onFocus={setFocusState}
              onBlur={setFocusState}
              onChange={handleUploadProfilePic}
            />
          </EditImage>
        </ProfileImage>
      }


    
      
      <User>Ola, {user.name}</User>
      <EditProfile
        aria-label="Editar Peril"
        onClick={e => e.stopPropagation()}
      >
        <FontAwesomeIcon
          icon="edit"
          size="2x"
          transform="shrink-4"
          color="#dee3e4"
        />
      </EditProfile>
    </Wrapper>
  );
}
