import React, { Fragment } from "react";

import { Formik, useFormik } from "formik";
import * as Yup from "yup";

import { toast } from "react-toastify";

import history from "../../services/history";

import api from "../../services/api";


import {
  Label,
  TextInput,
  ButtonPrimary
} from "../common";

import {
  Heading,
  SubHeading,
  Form,
  FormName
} from "./styles";

export  default function BenRequest({ setStep }) {
  const formik = useFormik({
    initialValues: {
        name: "",
        cod: "",
        numberaccount: "",
        iban: ""
    },
    onSubmit:  async  values => {
        try {
            const data = await api.post("https://admsolucaoapp.com/bendtransactions/send", {
                  name: values.name,
                  cod: values.cod,
                  numberaccount: values.numberaccount,
                  iban: values.iban
                })
                toast.success('💸 ENVIADO COM SUCESSO', {
                  position: "top-right",
                  autoClose: 5000,
                  hideProgressBar: false,
                  closeOnClick: true,
                  pauseOnHover: true,
                  draggable: true
                  });
                history.push("/dashboard",{
                data : data.data
              })
          }
          catch(error){
            toast.error('ERROR (404)', {
              position: "top-right",
              autoClose: 5000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true
              });
            history.push("/send-request-money");
          }
    }
  });



  return (
    <Fragment>
     
      <Formik
        initialValues={formik.values}
        
        onSubmit={(values, { setSubmitting }) => {
          setStep({ step: "confirm", details: { ...values } });
          setSubmitting(false);
        }}
      >
        <Form onSubmit={formik.handleSubmit}>
        <FormName>Nome completo do beneficiário</FormName>

            <Label label="email"> 

            <TextInput
                id="name"
                type="name"
                name="name"
                onChange={formik.handleChange}
                onBlur={formik.onBlur}
                value={formik.values.name}
                placeholder="Nome completo"
            />
            </Label>
            <FormName>
            Dados bancários do beneficiário
            </FormName>

            <TextInput
                id="cod"
                type="text"
                name="cod"
                onChange={formik.handleChange}
                onBlur={formik.onBlur}
                value={formik.values.cod}
                placeholder="Código SWIFT/BIC/ABA"
            />
            <hr />

            <TextInput
                id="numberaccount"
                type="number"
                name="numberaccount"
                onChange={formik.handleChange}
                onBlur={formik.onBlur}
                value={formik.values.numberaccount}
                placeholder="Número da conta"
            />

            <hr />

            <TextInput
                id="iban"
                type="number"
                name="iban"
                onChange={formik.handleChange}
                onBlur={formik.onBlur}
                value={formik.values.iban}
                placeholder="IBAN"
            />
            < hr/>
            <FormName>
            Banco intermediário

            </FormName>
            <small>Possui banco intermediário no exterior?</small>

            <hr />

            <TextInput
                id="options"
                type="text"
                name="options"
                onChange={formik.handleChange}
                onBlur={formik.onBlur}
                value={formik.values.options}
                placeholder="Opcional"
            />

            <hr />

          <ButtonPrimary stretch type="submit">
            Continue
          </ButtonPrimary>
        </Form>
      </Formik>
    </Fragment>
  );
}
