import React from "react"
import features from "./features"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { Wrapper, Contents, Row, ButtonLink } from "../../common"
import { Heading, SubHeading, Feature, Title, Description } from "./styles"

function WhyChoose() {
  return (
    <Wrapper>
      <Contents>
        <Heading>Por que você deve escolher Cambio Online?</Heading>
        <SubHeading>Os 4 principais motivos pelos quais você deve usar um Cambio Online para gerenciar seu dinheiro.
        </SubHeading>
        <Row>
          {features.map((feature, index) => (
            <Feature key={index}>
              <FontAwesomeIcon
                icon={feature.icon}
                size="3x"
                transform="shrink-1"
                flip={feature.flipIcon}
                color="#2dbe60"
              />
              <Title>{feature.title}</Title>
              <Description>{feature.description}</Description>
              <ButtonLink href="/signup">
                Saber mais{" "}
                <FontAwesomeIcon
                  icon="chevron-right"
                  transform="down-1 shrink-3 right-4"
                />
              </ButtonLink>
            </Feature>
          ))}
        </Row>
      </Contents>
    </Wrapper>
  )
}

export default WhyChoose
