import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";
import TransactionStatus from "./Status";
import ModalDash from "../../ModalDash";
import { Modal } from "../../common/Modal";

import { Row, Col } from "../../common";
import {
  Wrapper,
  TransactionName,
  TransactionDescription,
  TransactionAmount,
  Date,
  Day,
  Month,
  Amount,
  Currency,
  Sign
} from "./styles";

import api from "../../../services/api";

export function Transaction({ location }) {
  const [showModal, setShowModal] = useState(false);
  const closeModal = () => setShowModal(false);
  const openModal = () => setShowModal(true);

  const [recentActivitys, setRecentActivitys] = useState([]);
  const [activitysPosition, setActivitysPosition] = useState("");



  useEffect(() => {
    (async () => {
      const result = await api.get("/transactions/send/clients");
      const data = result.data;

      

      if (data.length) {
        setRecentActivitys(data);
      }
    })();
  }, []);

  useEffect(()=> {
    (async()=>{
        const result = await api.get("/users");
        const data = result.data;
       
      })();
    }, []);


  const handleWrapper = position => {
    setActivitysPosition(position);
    openModal();
  };

  return (
    <>
      {recentActivitys.map((activity, i) => (
        <Wrapper
          onClick={() => {
            handleWrapper(i);
          }}
          key={i}
        >

         
          <Row>
           
            <Col flexSM="4" flexMD="2" flex="5"  
            
            >
              <TransactionName>Cambio Online Bank</TransactionName>
              <TransactionDescription>
                {activity.email}
              </TransactionDescription>
            </Col>
            <Col flexSM="4" flexMd="2" flex="5">
              ${activity.recipient}
            </Col>
            <Col flexSM="0" flexMD="2" flex="2">
              <TransactionStatus status="processing" />
            </Col>
            <Col flexSM="3" flexMD="2" flex="2">
              <TransactionAmount>
                <Currency>({activity.recipientCurrency})</Currency>
              </TransactionAmount>
            </Col>
          </Row>
    
         
         
        </Wrapper>
      ))}
        
      {showModal && (
        <Modal closeModal={closeModal} noCloseButton>
          {
            <ModalDash
              data={recentActivitys[activitysPosition]}
            
              closeModal={closeModal}
            />
          }
        </Modal>
      )}
    </>
  );
}
