import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  Wrapper,
  Banner,
  BannerContents,
  BannerTransactionName,
  BannerTransactionAmount,
  BannerTransactionDate,
  Details,
  Divider,
  Title,
  TransactionContents,
  List,
  CloseButton,
  AccessibleText,
  Item,
  TransactionAmount,
  TransactionTotal,
  Total
} from "./styles";

export default function BenDash({ closeModal, data }) {
  return (
    <Wrapper onClick={e => e.stopPropagation()}>
      <Banner>
        <BannerContents>
          <FontAwesomeIcon icon="building" size="5x" color="#ffffff" />
          <BannerTransactionName>Cambio Online Bank </BannerTransactionName>
          <BannerTransactionAmount></BannerTransactionAmount>
          <BannerTransactionDate></BannerTransactionDate>
        </BannerContents>
      </Banner>
      <Details>
        
        <Title>Dados do Beneficiário

        <CloseButton aria-label="close modal" onClick={closeModal}>
            <FontAwesomeIcon icon="times" size="xs" />
            <AccessibleText>Close</AccessibleText>
          </CloseButton>
        </Title>
        <Divider mt="1rem" mb="1rem" />
        <TransactionContents>
          <List>
            <Item medium>
              Nome:
              <TransactionAmount>
                {data.name}
              </TransactionAmount>
            </Item>
          </List>
          <Divider mt="1rem" mb="0.5rem" />
          <TransactionTotal>
            <Total>
              COD:{data.cod} 
            </Total>
            
          </TransactionTotal>
          
          <List>
            <Item medium>
            CONTA: {data.numberaccount}
            </Item>
            <Item color="#8e9a9d"></Item>
          </List>
          <List>
            <Item medium>IBAN</Item>
            <Item color="#8e9a9d">{data.iban}</Item>
          </List>

          <List>
            <Item medium>Status: {data.status ? "aprovado" : "pendente"}</Item>
            <Item color="#8e9a9d"></Item>
          </List>
        </TransactionContents>
      </Details>
    </Wrapper>
  );
}
