import styled, { keyframes }from "styled-components"

export const Heading = styled.h2`
  font-family: "rubikregular", sans-serif;
  text-align: center;
`

export const SubHeading = styled.p`
  text-align: center;
  font-size: 1.125rem;
`

export const FormName = styled.h3`
  font-family: "rubikregular", sans-serif;
  font-size: 1.3125rem;
  margin-bottom: 1rem;
`

const transform = keyframes`
  from {
    transform: translateY(-20%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
`
export const Form = styled.form`
  width: 75%;
  max-width: 320px;
  hight: 80px;
  margin: 1.5rem auto 0.5rem;
  padding 1.5rem;
  border-radius: 0.25rem;
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
  background-color: #ffffff;
`

export const ExchangeRate = styled.p`
  color: #8e9a9d;
  text-align: center;
`

export const Rate = styled.span`
  font-family: "rubikmedium", sans-serif;
`

export const Divider = styled.hr``

export const TotalFees = styled.p`
  position: relative;
  margin-top: 0;
  margin-bottom: 0.25rem;
`

export const TotalToPay = styled(TotalFees)`
  font-family: "rubikmedium", sans-serif;
  margin-bottom: 1rem;
`

export const Fee = styled.span`
  position: absolute;
  font-size: 1rem;
  right: 0;
`

export const AccessibleText = styled.span`
  border: 0 !important;
  clip: rect(0 0 0 0) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important;
`



export const CloseButton = styled.button`
  position: absolute;
  top: 210px;
  right: 15px;
  width: fit-content;
  padding: 0;
  background-color: transparent;
  border: none;
  animation: ${transform} 1s linear 1;
  @media (min-width: 375px) {
    top: 280px;
  }
  @media (min-width: 414px) {
    top: 230px;
  }
  @media (min-width: 768px) {
    top: 280px;
  }
  @media (min-width: 1024px) {
    top: 430px;
    right: 90px;
  }
  @media (min-width: 1600px) {
    top: 150px;
    right: 380px;
  }
`